// the whole page is in column where each section is stacked 
.page__container{
    display: flex; 
    flex-direction: column;
    width: 100%;
}

// page section can be a whole or with partition (in this case is just two partition
.page__section{
    display: flex;
    flex-direction: row;
    margin-bottom: 72px;
    &:last-child{
        margin-bottom: 0;
    }
}

// list in the page 
.page__list--dark-bg{
    display:flex;
    flex-direction: column;
    background-color: $very-dark-blue;
    width: 30%;
    padding: 24px;
}

.list__title{
    font-family: $primary-font;
    font-size: 1.7em;
    font-weight: 800;
    margin-bottom: 0;
}
.list__title--dark-bg{
    color: $primary-soft-orange;
}

// the foundation of an article is here 
.page__article{
    display: flex;
}

// the variants of each article, it can be with feature, sideways or stacked
.page__article--feature{
    flex-direction: column;
    margin-right: 32px;
    width: 70%;
}

.page__article--sideways{
    flex-direction: row;
}

// inside the div of page article stack will not have an article seperator
.page__article--stack{
    flex-direction: column;
    &:last-child{
        .article__seperator{
            display: none;
        }
    }
}


// general css for articles
.article__title{
    font-size: 1em;
    font-weight: 800;
    font-family: $primary-font;
    color: $very-dark-blue;
    margin-bottom: 0;
    &:hover{
        cursor: pointer;
        color: $primary-soft-red
    }
}
// for dark background
.article__title--dark-bg{
    font-size: 1.2em;
    color: $off-white;
    &:hover{
        color: $primary-soft-orange;
        cursor: pointer;
    }
}

// articles at the bottom list
.article__image-wrapper{
    width: 30%;
    margin-right: 12px;
    img{
        width: 100%;
        height: 100%;
    }
}

.article__content{
    width: 70%;
    margin-right: 12px;
}

.article__numbering{
    font-weight: 800;
    color: $grayish-blue;
    font-size: 2em;
    margin-bottom: 0;
    margin-top: 0;
}

.article__subtitle{ 
    font-size: 0.9em;
    font-family: $primary-font;
    color: $dark-grayish-blue;
    text-align: left;
    line-height: 1.5;
    margin-bottom: 0;
}

.article__seperator{
    background-color: $grayish-blue;
    width: 100%;
    border: solid 0.5px $grayish-blue;
    margin-top: 12px;
}

// feature articles css - the css that specifically for feature articles
.article__image-wrapper--feature{
    // the image is following the parent width and height,
    img{
        width: 100%;
        height: 100%;
    }
}

.article__content-container--feature{
    margin-top: 48px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.article__title-wrapper{
    width: 50%;
}

.article__content-wrapper{
    height: 100%;
    width: 50%;

}

.article__title--feature{
    font-size: 3em;
    margin-top: 0;
    &:hover{
        color: $very-dark-blue;
        cursor: default;
    }
}

.article__content--feature{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

@media only screen and (max-width: 375px)  {
    .page__section{
        flex-direction: column;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 48px;
    }

    .page__article--feature{
        width: 100%; 
        margin-right: 0;
        margin-bottom: 48px ;
    }

    .article__title-wrapper, .article__content-wrapper{
        width: 100%;
    }

    .article__title--feature{
        font-size: 2em;
    }

    .article__content-container--feature{
        margin-top: 8px;
        flex-direction: column;
    }
    .page__list--dark-bg{
        width: 90%;
    }

    .page__article--sideways{
        margin-bottom: 12px;
    }
}
